// 90度，180度等为断点，旋转到阈值内会自动吸附到断点度数
const THRESHOLD = 3;
const fitRotate = (rotate, threshold = THRESHOLD) => {
  const noun = rotate % 90;
  const num = Math.floor(rotate / 90);
  if (noun >= (90 - threshold)) {
    return 90 * (num + 1);
  }
  if (noun <= threshold) {
    return 90 * num;
  }
  return rotate;
}

export default fitRotate;
